body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f2f2f2
}

h1 {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* стилизуем нав-бар */
.nav-wrapper {
  /* text-align: center; */
  padding: 0 4rem 0 4rem;
}
.logoName {
  font-size: 2rem;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}


/* стилизуем основной контейнер. компонент Main */
.content {
  position: relative;
  min-height: calc(100vh - 64px - 70px);  
  padding: 2rem 0;
}


/* стилизуем отображения товаров по системе grid */
.category-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

/* стилизуем карточку товара */
.card {
  display: flex;
  flex-direction: column;
  background-color: rgba(232, 232, 232, 0.971);
  /* border: 1px solid black; */
}
/* задаю кнопке расположение снизу */
.card-content {
  flex-grow: 1;
}
.card-content p {
  font-family: 'Roboto';
} 

/* названия блюд */
.card-title {
  font-family: 'Segoe UI';
  border-bottom: 1px solid black;
}



/* адаптируем размер Лого под размер экрана */

  @media (max-width: 515px) {
    .logoName {
      font-size: 4vw;
    }

    .home-about {
      font-size: 3.5vw !important;
    }
  }